<template>
  <v-row align-content="center" justify="center" align="center" class="h-100">
    <v-col cols="12" md="4" lg="4" xl="3">
      <v-img
        :src="require(`@/assets/images/logos/logo_${getTheme}.png`)"
        contain
        class="mb-10"
      ></v-img>
      <v-card elevation="5" outlined shaped>
        <v-card-title>
          <v-row justify="center">
            <v-col class="text-center">
              <!--              <v-img-->
              <!--                height="60"-->
              <!--                src="@/assets/images/logos/loddos-logo-long-light.png"-->
              <!--                contain-->
              <!--              ></v-img>-->
              <h2 class="mt-2">Login</h2>
            </v-col>
          </v-row>
        </v-card-title>
        <ValidationObserver ref="dmForm">
          <v-form @submit.prevent="onLogin">
            <v-card-text>
              <dm-input
                v-model="credentials.email"
                label="Email"
                rules="required"
                prepend-inner-icon="mdi-email-outline"
                autocomplete="username"
              >
                <template></template>
              </dm-input>
              <dm-input
                v-model="credentials.password"
                :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                label="Password"
                rules="required"
                :type="show ? 'text' : 'password'"
                prepend-inner-icon="mdi-lock-outline"
                @click:append="show = !show"
                autocomplete="new-password"
              >
              </dm-input>
            </v-card-text>
            <v-card-actions>
              <v-btn color="primary" type="submit" block :loading="loading">
                Login
              </v-btn>
            </v-card-actions>
            <v-card-actions>
              <v-btn link color="warning" text @click="forgotPassword">
                Forgot Password?
              </v-btn>
            </v-card-actions>
          </v-form>
        </ValidationObserver>
      </v-card>
    </v-col>
  </v-row>
</template>

<!-- Load login custom page styles -->
<style lang="scss"></style>

<script>
import { mapActions } from "vuex";
import DmInput from "@/components/common/form/DmInput";

export default {
  name: "auth",
  components: { DmInput },
  data: () => ({
    credentials: {
      email: "",
      password: "",
    },
    show: false,
    loading: false,
  }),
  computed: {
    getTheme() {
      if (this.$vuetify.theme.dark) {
        return "grey";
      } else return "original";
    },
  },
  methods: {
    ...mapActions({
      login: "auth/login",
      verifyAuth: "auth/verifyAuth",
    }),
    onLogin() {
      this.$refs.dmForm.validate().then((success) => {
        if (!success) {
          return;
        }
        this.loading = true;
        this.login(this.credentials)
          .then(() => {
            this.verifyAuth();
            let self = this;
            setTimeout(function () {
              self.loading = false;
              self.$router.push("/");
            }, 500);
          })
          .catch((error) => {
            this.loading = false;
            this.$toast.setMessage(error.data.detail, 1);
          });
      });
    },
    forgotPassword() {
      this.$router.push("forgot-password");
    },
  },
};
</script>
<style>
.h-100 {
  min-height: 100vh;
}
</style>
